import React, { Component } from 'react';
import './App.css';
import { connect } from 'react-redux';
import { Route } from 'react-router-dom';

import Home from './components/home/home.jsx';
import SignupPage from './components/signup/signupPage.jsx';
import LoginPage from './components/login/loginPage.jsx';
import Article from './components/article/article.jsx';
import UserProfilePage from './components/user/userProfilePage.jsx';
import CreateArticlePage from './components/createArticle/createArticlePage.jsx';
import EditArticlePage from './components/editArticle/editArticlePage.jsx';
import { Navbar } from './components/navbar/navbar.jsx';

import {
  setAuthorized,
  setCurrentUserProfile,
  articlesLoad,
} from './redux/actions/actionCreators';
import { currentUserRequest, loadAllArticlesRequest, loadUserArticlesRequest } from './services/serverApi.js';
import { isAuth } from './services/localStorageApi.js';
import { basePath } from './services/paths.js';

class App extends Component {
  updateCurrentUserInStore = async () => {
    const { setAuth, setCurrentUser } = this.props;
    if (isAuth()) {
      const response = await currentUserRequest();
      const { user } = await response.data;
      setCurrentUser(user);
      setAuth(true);
    }
  };

  getListArticles = async () => {
    const { showMode, showQuantity, setArticlesToStore } = this.props;
    if (showMode === '') {
      const response = await loadAllArticlesRequest(showQuantity);
      const { articles, articlesCount } = await response.data;
      setArticlesToStore(articles, articlesCount);
    } else {
      const response = await loadUserArticlesRequest(showQuantity, showMode);
      const { articles, articlesCount } = await response.data;
      setArticlesToStore(articles, articlesCount);
    }
  };

  componentDidMount() {
    this.updateCurrentUserInStore();
    this.getListArticles();
  }

  render() {
    return (
      <div className="app">
        <Navbar />
        <Route exact path={basePath} component={Home} />
        <Route path={`${basePath}/signup`} component={SignupPage} />
        <Route path={`${basePath}/login`} component={LoginPage} />
        <Route path={`${basePath}/add`} component={CreateArticlePage} />
        <Route exact path={`${basePath}/articles/:slug`} component={Article} />
        <Route exact path={`${basePath}/user/:username`} component={UserProfilePage} />
        <Route path={`${basePath}/articles/:slug/edit`} component={EditArticlePage} />
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  const { showQuantity, currentUser, currentPage, showMode } = state;
  return {
    showQuantity,
    currentUser,
    currentPage,
    showMode,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    setArticlesToStore: (articles, articlesCount) =>
      dispatch(articlesLoad(articles, articlesCount)),
    setCurrentUser: (user) => dispatch(setCurrentUserProfile(user)),
    setAuth: (auth) => dispatch(setAuthorized(auth)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
