import {
  ARTICLES_LOAD,
  CURRENT_ARTICLE_LOAD,
  SET_AUTHORIZED,
  CURRENT_USER_PROFILE_LOAD,
  SET_CURRENT_MENU_ITEM,
  SET_FILTER_BY_AUTHOR
} from '../actions/actionTypes';

const initialState = {
  listArticles: [],
  articlesCount: 0,
  isAuthorized: false,
  filterByAuthor: '',
  currentUser: {},
  showQuantity: 9,
  currentMenuItem: '',
};

export default function rootReducer(state = initialState, action) {
  const {
    type,
    listArticles,
    articlesCount,
    currentArticle,
    isAuthorized,
    currentUser,
    filterByAuthor,
    currentMenuItem,
  } = action;
  switch (type) {
    case ARTICLES_LOAD:
      return {...state, listArticles, articlesCount};
    case CURRENT_ARTICLE_LOAD:
      return {...state, currentArticle};
    case SET_AUTHORIZED:
      return {...state, isAuthorized};
    case SET_FILTER_BY_AUTHOR:
      return {...state, filterByAuthor};
    case CURRENT_USER_PROFILE_LOAD:
      return {...state, currentUser};
    case SET_CURRENT_MENU_ITEM:
      return {...state, currentMenuItem};
    default:
      return state;
  }
}
